<template>
    <yd-layout class="yearSalary">
        <div class="salaryMain">
            <div class="salaryHeader headerHight">
                <div class="ft">当前累计实际收入</div>
                <div class="tt">¥{{yearSalary.sum}}</div>
                <!-- <div class="zgs">总公司正式工薪资组</div> -->
                <!-- <div class="img" @click="passShow=true">{{tabTxt}}</div> -->
            </div>
            <div class="stitle stitle2" @click="statusPopup=true">
                {{Year}}年合计收入
            </div>
            <div class="salaryContent">
                <yd-flexbox>
                    <yd-flexbox-item class="fLine" @click.native="centerShow=true">本年度工资应发合计</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.yf"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">年终奖</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.y_yf"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">总经理奖励</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.zjl"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">其他</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.other"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <yd-flexbox-item>代扣费用</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.dk"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">个人社保</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.shbx"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">个人公积金</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.gjj"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">补充公积金</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.bcgjj"></yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="tflexbox">
                    <yd-flexbox-item class="no_fLine">个人所得税</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yearSalary.tax"></yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999" v-if="statusPopup==false">
            <yd-tabbar-item link="/monthSalary" title="月度薪酬">
                <yd-icon name="month" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item link="" title="年度薪酬" active>
                <yd-icon name="year" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" @click="statusPopup=false">取消</div>
                <div class="rightBtn" @click="statusPopup=false;Year=theYear">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" @click="theYear=currentdate" :class="{active:theYear==currentdate}">{{currentdate}}年度</li>
                    <li class="li" @click="theYear=currentdate-1" :class="{active:theYear==currentdate-1}">{{currentdate - 1}}年度</li>
                    <li class="li" @click="theYear=currentdate-2" :class="{active:theYear==currentdate-2}">{{currentdate - 2}}年度</li>
                    <li class="li" @click="theYear=currentdate-3" :class="{active:theYear==currentdate-3}">{{currentdate - 3}}年度</li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="centerShow" position="center" width="6rem" height="4.73rem" class="ydPopup">
            <div>
                <div style="height:1.1rem;background-color:#fff;" class="popup">
                    当年累计税前收入
                </div>
                <div class="details" style="background-color:#fff;">
                    “当前累计实际收入”=“本年度工资应发合计”-“代扣费用”
                </div>
                <div class="return" @click="centerShow=false">
                    <img src="../../../static/images/close@2x.png">
                </div>
                </div>
        </yd-popup>
        <yd-popup v-model="passShow" position="center" width="6rem" height="5.5rem" class="ydPopup">
            <div class="passDiv">
                <div class="passwordTitle">请输入人力资源系统薪酬查询密码：</div>
                <div>
                    <yd-input slot="right" type="password" v-model="password" placeholder="请输入密码"></yd-input>
                </div>
                <div class="passBottom">
                    <div class="passComf1" @click="queryYearBtn(password)">确认</div>
                </div>
                <div class="explain">说明：若需要修改密码，请登录人力资源系统PC端进行修改</div>
            </div>
            <div class="return" @click="passShow=false">
                <img src="../../../static/images/close@2x.png">
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import md5 from 'md5';
export default {
    name: 'yearSalary',
    data() {
        return {
            currentdate: '',
            statusPopup: false,
            Year: '',
            theYear: '',
            centerShow: false,
            passShow: false,
            tabTxt: '查看工资',
            password: '',
            yearSalary: {
                dk: '****',
                tax: '****',
                y_yf: '****',
                yf: '****',
                zjl: '****',
                gjj: '****',
                bcgjj: '****',
                shbx: '****',
                other: '****',
                sum: '****'
            }
        }
    },
    mounted() {
        document.setTitle('薪酬管理');
        let vm = this;
        vm.currentdate = vm.formatDate(new Date(), 'yyyy');
        vm.theYear = vm.Year = vm.currentdate;
        // let pscookie = vm.getCookieValue('salary');
        // if (pscookie != '') {
        //     vm.passShow = false;
        //     vm.CheckWagesByYear(vm.Year, pscookie);
        // } else {
        //     // vm.passShow = true;
        // }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    watch: {
        Year: function() {
            let vm = this;
            let pscookie = vm.getCookieValue('salary');
            if (pscookie != '') {
                // vm.passShow = false;
                vm.CheckWagesByYear(vm.Year, pscookie);
            } else {
                vm.passShow = true;
            }
        }
    },
    methods: {
        addCookie(name, value, minute) {
            var _name = escape(name);
            var _value = escape(md5(value));
            var expires = new Date();
            expires.setTime(expires.getTime() + minute * 60 * 1000);
            var _expires = (typeof minute) == 'string' ? '' : ';expires=' + expires.toUTCString();
            document.cookie = _name + '=' + _value + _expires;
        },
        getCookieValue(name) {
            var _name = escape(name);
            var allcookies = document.cookie;
            _name += '=';
            var pos = allcookies.indexOf(_name);
            if (pos != -1) {
                var start = pos + _name.length;
                var end = allcookies.indexOf(';', start);
                if (end == -1) end = allcookies.length;
                var value = allcookies.substring(start, end);
                return (value);
            } else {
                return '';
            }
        },
        deleteCookie(name) {
            var _name = escape(name);
            var _expires = new Date(0);
            document.cookie = _name + '=' + ';expires=' + _expires.toUTCString();
        },
        CheckWagesByYear(year, password) {
            let vm = this;
            vm.post(vm.conf.api + '/CheckWages/CheckWagesByYear', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                year: year,
                password: password,
                empcode: vm.getUserInfo.empcode
            }, function(res) {
                vm.password = '';
                if (res.isSuccess == true || res.isSuccess == 'true') {
                    vm.passShow = false;
                    vm.yearSalary = res.jsonData;
                } else {
                    vm.deleteCookie('salary');
                    vm.$dialog.toast({
                        mes: res.errorMessage,
                        timeout: 2000
                    });
                }
            }, function(res) {
                vm.deleteCookie('salary');
            });
        },
        getYearSalaryDetail(year, password) {
            let vm = this;
            vm.post(vm.conf.api + '/CheckWages/getYearSalaryDetail', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                year: year,
                password: password,
                empcode: vm.getUserInfo.empcode
            }, function(res) {
                vm.passShow = false;
                vm.yearSalary = res.jsonData[0];
            });
        },
        open() {
            this.$refs.datetime.open();
        },
        queryYearBtn(password) {
            let vm = this;
            vm.addCookie('salary', password, 2);
            if (password == '') {
                vm.$dialog.toast({
                    mes: '请输入密码',
                    timeout: 2000
                });
            } else {
                vm.CheckWagesByYear(vm.Year, md5(password));
            }
        }
    }
}
</script>
<style lang="less">
@import 'salary.less';

.yearSalary {
    .fLine {
        color: #999;
        background: url(../../../static/images/salary/remark@2x.png) no-repeat;
        background-size: .3rem;
        background-position: 2.9rem center;
    }

    .no_fLine {
        color: #999;
    }

    .tflexbox {
        border-bottom: .02rem dashed #eaeaea !important;
    }

    .yd-icon-month:before,
    .yd-icon-year:before {
        display: block;
        content: "";
    }

    .yd-icon-month:before {
        width: 0.5rem;
        height: 0.5rem;
        background: url(../../../static/images/salary/monthSalary1@2x.png) no-repeat 0 0;
        background-size: 0.5rem;
    }

    .yd-icon-year:before {
        width: 0.5rem;
        height: 0.5rem;
        background: url(../../../static/images/salary/yearSalary@2x.png) no-repeat 0 0;
        background-size: 0.5rem;
    }

    .yd-popup-center {
        overflow: hidden;
        background-color: initial !important;
        border-radius: 5px;
        // padding: 0 .4rem;
    }

    .popup {
        height: 1.1rem;
        line-height: 1.1rem;
        color: #333;
        font-size: .36rem;
        text-align: center;
        border-bottom: 1px dashed #eaeaea;
    }

    .details {
        padding-left: .54rem;
        padding-right: .54rem;
        padding-top: .25rem;
        line-height: .7rem;
        font-size: .32rem;
        color: #666;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
        padding-bottom: .2rem;
    }

    .return {
        text-align: center;
        width: .74rem;
        border-radius: 50%;
        background: #bababa;
        height: .74rem;
        margin: 0 auto;
        margin-top: .2rem;
        padding-top: .23rem;

        img {
            width: .3rem;
            height: .3rem;
        }
    }

    .yd-input {
        height: .6rem;
        background-color: #f4f4f4;
        padding-left: .18rem;
        border-radius: .07rem;
    }

    .passBottom {
        width: 100%;
        height: .43rem;
        background-color: #fff;
        margin-top: .4rem;
        // padding-bottom: .2rem;
    }

    .passComf1 {
        height: .43rem;
        width: 1.65rem;
        line-height: .43rem;
        text-align: center;
        font-size: .24rem;
        background-color: #59a5fd;
        color: #fff;
        margin: 0 auto;
        border-radius: 3px;
    }

    .passDiv {
        background-color: #fff;
        padding: 0 .4rem;
        border-radius: 5px;
    }

    .yd-popup-content {
        border-radius: 5px;
        // background-color: #fff;
    }

    .passwordTitle {
        height: 1.1rem;
        line-height: 1.1rem;
        font-size: .32rem;
        color: #333;
        text-align: center;
    }

    .explain {
        font-size: .24rem;
        color: #666;
        margin-top: .5rem;
        padding-bottom: .6rem;
    }
}

.headerHight {
    height: 3.4rem !important;
}
</style>


